import React, {
	useEffect,
	useRef,
	useState,
} from "react";


import UseDelayInput from "../hooks/useDelayInput";
import { updStore, gen16CodeId } from "../stores/Utils";

import "./TextInput.css";


const SearchInputComponent = ({ fetchData, width, loading, change, keyFlds, callempty,placeholder }) => {
	const [searchQr, setSearchQ] = useState("");
	const searchRef = useRef();

	let deferredInput = UseDelayInput(searchQr);

	useEffect(() => {
		if (!deferredInput) {
			updStore(keyFlds, "");
			callempty && callempty()
		} else {
			updStore(keyFlds, deferredInput);
			fetchData(deferredInput);
		}
		return () => {
			//;
		};
	}, [deferredInput]);


	

	const handleInput = (v) => {
		setSearchQ(v.target.value);
		if (typeof change === "function") {
			change(v.target.value);
		}
	};

	return (
		<div>

			<input
                style={width? {width: width}: null}
				ref={searchRef}
				className="r7gAOb "
				type="text"
				//aria-autocomplete="both"
				aria-haspopup="false"
				auto-capitalize="off"
				auto-complete="off"
				auto-correct="off"
				spell-check="false"
				placeholder={placeholder || "buscar"}
				onChange={handleInput}
			/>
			{loading ?
				<div className="load_animation"/>
 			:<div style={{height:2}}/>}
		</div>
		
	);
};

export default SearchInputComponent;
